import React, { lazy } from 'react'
import {
  CBadge,
  CButton,
  CButtonGroup,
  CCard,
  CCardBody,
  CCardFooter,
  CCardHeader,
  CCol,
  Link,
  CProgress,
  CRow,
  CCallout
} from '@coreui/react'
// import {Submit_assignment} from '../../redux_containers/Container'
import {requestOptions,Mytime,Mydate,assignment} from '../Config'
import Upload from './Upload'

class Assignment extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        data: []
      };
    }
    componentDidMount() {
          fetch(assignment, requestOptions)
           .then((response) => response.json())
           .then((data) => {this.setState({data:data}) })
           .catch(error => console.log("Error detected: " + error))
       }


    render() {

        return  <> 
      <CRow>
        <CCol>
          <CCard>
           
            <CCardBody>
              

              <br />

              <table className="table table-hover  table-responsive-md">
                <thead className="thead-light">
                  <tr>
             
                    <th>S.no</th>
                    <th>Type</th>
                    <th>Title</th>
                    <th>Description</th>
                    <th>Deadline</th>               
                    <th>Due Date and time</th>               
                    <th className="">Action</th>
                 
                  </tr>
                </thead>
                <tbody>

            {this.state.data.map((list,index) => (
              <tr key={index}>
              
                <td  className="border-left" width="5%"><div>{index+1}</div></td>
                <td  width="15%" > {list.type} </td>
                <td  width="15%" > {list.title} </td>
                <td  width="20%" > {list.body} </td>
                <td> <div className="small text-muted" >{list.deadline}</div></td>
                
                <td className=" border-right">
                    <CRow className=" row">
                        <CCol   className="mb-3 col-12 col-lg-6">
                          <CButton block color="primary">View</CButton>
                        </CCol>
                        <CCol  className="mb-3 col-12 col-lg-6">
                           <Upload assignmentData={list}/>
                        </CCol>
                
                     </CRow>
                </td>
              </tr>
   ))}
                  
                </tbody>
              </table>

            </CCardBody>
          </CCard>
        </CCol>
      </CRow>
      </>;
        }

}

export default Assignment
