import React, { useState } from 'react';
// import {useDropzone} from 'react-dropzone';
import { CButton} from '@coreui/react'
import Dropzone from 'react-dropzone';
import {connect} from 'react-redux'
import {getFiles} from '../../service/actions/actions'
// function Accept(props) {

  class Accept extends React.Component {
    constructor() {
      super();
      this.state = {
        files: []
      };
    }
  
    onDrop = (files) => {
      this.setState({files})
    this.props.getFileHandler(files)
  
  } 
  


    render(){
  
      const files = this.state.files.map((file,index) => (
        <li key={index}>
         
          {file.name} - {file.size} bytes
        </li>
      ));
 
// console.log(props)

  return (
    <Dropzone onDrop={this.onDrop}>
    {({getRootProps, getInputProps}) => (
      <section className="container text-center">
        <div {...getRootProps({className: 'dropzone'})} className="border">
          <input {...getInputProps()} />
          <p>Drag 'n' drop some files here, or click to select files</p>
           
        <br/>
        <CButton color="info font-weight-bolder" size="lg" onClick={()=>this.onDrop(files)} >Select files</CButton>
        <br/>
        <br/>
        <br/>
        </div>
        <aside>
          <h4>Files</h4>
          <ul>{files}</ul>
        </aside>
      </section>
    )}
  </Dropzone>
  );

    }
}

// export default Accept;


const mapStateToProps=state=>({
  fileData:state.getFiles[0]
})
const mapDispatchToProps=dispatch=>({
  getFileHandler:data=>dispatch(getFiles(data))

})
export default connect(mapStateToProps,mapDispatchToProps)(Accept)
// export default Home;
