import React from 'react'
import {Link } from 'react-router-dom';
import ReactPlayer from 'react-player'
import {
  CBadge,
  CRow,
  CCol
} from '@coreui/react'
import Banner from './Banner.png'
import {Mydate, requestOptions,videoUrl} from '../Config'

class Videos extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      videoList: [],
    };
  }
  componentDidMount() {
    fetch(videoUrl, requestOptions)
    .then((response) => response.json())
    .then((data) => {this.setState({videoList:data})   })
    .catch(error => console.log("Error detected: " + error)) ;
    }

    render(){
      // console.log(this.state.videoList);
    return  <>
          <img src={Banner} width="100%"/>
          <br/><br/>
          <span className="h5"><strong>New Videos</strong></span> <br/><br/>
          <CRow>
             { this.state.videoList.map((video,index)=>( 
              <CCol sm="6" lg="3" key={index}>
                    <Link to="/playback" onClick={()=>this.props.getVideoHandler(video)}>
                      <div className='player-wrapper'>
                            <ReactPlayer
                              className='react-player'
                              url={[{src: `${this.state.video}`, type: 'video/mp4'} ]}
                              width='100%'
                              height='100%'
                              controls
                            />
                            <CBadge color="success" >New</CBadge>
                            <div><strong>{video.title}</strong></div>
                            <div className="small text-muted">
                                <span>New</span> | {video.batch}: {video.startDateTime.substr(0,10)}
                            </div>
                          </div>
                    </Link>     
                    </CCol>  
               ))}
          </CRow>
          <br/><br/>
         <span className="h5"><strong>Recommended for you</strong></span> <br/><br/>
          <CRow>
              { this.state.videoList.map((video,index)=>( 
                <CCol sm="6" lg="3" key={index}>
                      <Link to="/playback" onClick={()=>this.props.getVideoHandler(video)}>
                        <div className='player-wrapper'>
                              <ReactPlayer
                                className='react-player'
                                url={[{src: `${this.state.video}`, type: 'video/mp4'} ]}
                                width='100%'
                                height='100%'
                                controls
                              />
                              
                              <CBadge color="success" >New</CBadge>
                              <div><strong>{video.title}</strong></div>
                              <div className="small text-muted">
                                  <span>New</span> | {video.batch}: {video.startDateTime.substr(0,10)}
                              </div>
                          
                            </div>
                      </Link>     
                      </CCol>  
                 ))} 
             </CRow>

  <br/><br/>
      <span className="h5"><strong>Previous Videos</strong></span>
    <br/><br/>
  <div className="row">
        <div className="col-sm">
          <div className="row">
            <div className="col-sm "> 
                 <video className="ml-0" width="100%"  controls ><source src="./Videos/video1.mp4" type="video/mp4"/></video>
                        <div><strong>IIT Matchmatics</strong></div>
                        <div className="small text-muted">
                            <span>New</span> | Registered: Jan 1, 2015
                        </div>
            </div>
          <div className="col-sm "> 
                <video className="ml-0" width="100%"  controls ><source src="./Videos/video1.mp4" type="video/mp4"/></video>
                            <div><strong>IIT Matchmatics</strong></div>
                            <div className="small text-muted">
                                <span>New</span> | Registered: Jan 1, 2015
                            </div>
          </div>
          </div>
        </div>
        <div className="col-sm">
        <div className="row">
            <div className="col-sm "> 
                 <video className="ml-0" width="100%"  controls ><source src="./Videos/video1.mp4" type="video/mp4"/></video>
                        <div><strong>IIT Matchmatics</strong></div>
                        <div className="small text-muted">
                            <span>New</span> | Registered: Jan 1, 2015
                        </div>
            </div>
          <div className="col-sm "> 
                <video className="ml-0" width="100%"  controls ><source src="./Videos/video1.mp4" type="video/mp4"/></video>
                            <div><strong>IIT Matchmatics</strong></div>
                            <div className="small text-muted">
                                <span>New</span> | Registered: Jan 1, 2015
                            </div>
          </div>
          </div>
        </div>
  </div>
    
    </>
  
}

}

export default Videos
