import React, { Component } from "react";
import {Mytime} from '../Config'
import { Link } from 'react-router-dom'


export default class CalenderApi extends Component {


  render() {
    console.log(this.props)

    return  <>
           {this.props.announcement.map((list,index) => (
                    <div className="card border-0" key={index}>
                
                <div className="card-body">
                  <h5 className="card-title font-weight-bold">{list.batch}</h5>
                  <p className="small text-muted">{Mytime(list.startDateTime)} to {Mytime(list.endDateTime)}</p>
                  <p className="font-weight-bold">Faculty : {list.teacher}</p>
                  <p className="">Details:</p>
                  <p className="card-text">{list.description}...</p>
                  <a target="_blank" href={list.meetlink} className="btn btn-info card-link">Join</a>
                  {/* <Link to="/dashboard/Preread" ><div onClick={()=>this.props.getTodaySessionHandler(list)}>{list.batch}</div></Link> */}
                
                    
                  <Link to="/dashboard/Preread"className="btn btn-warning card-link" ><div onClick={()=>this.props.getTodaySessionHandler(list)}>Preread</div></Link>
               
                </div>
                <hr/>
              </div>
                  ))}  
  </>
  }
}

