import React from 'react';
import {video} from './redux_containers/Container'
import {Upload_assignment} from './redux_containers/Container'

// var video = React.lazy(() => import('../src/redux_containers/Container'));
const playback = React.lazy(() => import('../src/redux_containers/PlaybackContainer'));
const announcement = React.lazy(() => import('./views/announcements/Announcement'));
const assignment = React.lazy(() => import('./views/assignments/Assignment'));
const login = React.lazy(() => import('./views/Login/Login'));
const Calender = React.lazy(() => import('./views/calender/Calender'));
const All_preread = React.lazy(() => import('../src/redux_containers/PrereadContainer'));
const My_assignment = React.lazy(() => import('./views/Myassignments/My_assignment'));
const Dashboard = React.lazy(() => import('./views/dashboard/Dashboard'));




const routes = [
  { path: '/', exact: true, name: 'Home' },
  { path: '/videos', name: 'video', component: video },
  { path: '/playback', name: 'playback', component: playback },
  { path: '/announcement', name: 'announcement', component: announcement },
  { path: '/assignment', name: 'assignment', component: Upload_assignment },
  { path: '/playback', name: 'playback', component: playback },
  { path: '/student/login', name: 'login', component: login },
  { path: '/calender', name: 'Calender', component: Calender },
  { path: '/dashboard/Preread', name: 'preread', component: All_preread },
  { path: '/myassignment', name: 'myassignment', component: My_assignment },
  { path: '/dashboard', name: 'Dashboard', component: Dashboard },

 
];

export default routes;
