import React from 'react';
import axios from 'axios'; 
import {connect} from 'react-redux'
import {getFiles} from '../../service/actions/actions'
import {
  CButton,
  CCard,
  CCardBody,
  CCol,
  CModal,
  CModalBody,
  CModalFooter,
  CModalHeader,
  CModalTitle,
  CRow,
  CFormGroup,
  CInput,
  CLabel,
  CSelect,
} from '@coreui/react'
import Accept from './Accept'
import {studentID,studentName,batchID,batchName,uploadMyAssignment} from '../Config'

class Upload extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
         large: false
      };
    }
    handleChange =(event)=>{
        const name = event.target.name;
        const value = event.target.value;
        this.setState({
            [name]: value
        })
    }

    handleSet = () =>{
        // console.log(this.state.large);
        this.setState({
            large: !this.state.large
        })
    }
    handleContinue = (event)=>{
        event.preventDefault();

  // Create an object of formData 
  const formData = new FormData(); 
       
  var curr =    new Date();

  // Update the formData object 
  formData.append('student', studentID);
  formData.append('student_name', studentName);
  formData.append('batch', batchID);
  formData.append('batch_name', batchName);
  formData.append('assignment_name',this.state.title || this.props.assignmentData.title);
  formData.append('assignment_id',this.props.assignmentData.id);
  formData.append('file',this.props.fileData);
  formData.append('submission_time',curr.toISOString());
  formData.append('scheduled_time', this.props.assignmentData.dateTime);

axios.post(uploadMyAssignment, formData)
    .then(data => {
      alert("Your Assignment Upload Successfully");
      window.location.reload();
    })    
    .catch(error => console.log("Error detected: " + error))


}

render() {


    const large = this.state.large;
    // console.log(this.props)
    const assignmentData = this.props.assignmentData;
    // console.log(this.props)

 
        return  <>     

           <CButton block variant="outline" color="success" onClick={this.handleSet.bind(this)} >Upload</CButton>
       
        <CModal show={this.state.large} onClose={() => this.handleSet(large)} size="lg">
              <CModalHeader closeButton><CModalTitle>Upload File</CModalTitle></CModalHeader>
              <CModalBody >
                 <Accept/>
                <CRow>
                     <CCol xs="12" sm="12">
                        <CCard>
                            <CCardBody>
                                <CFormGroup row className="my-0">
                                <CCol xs="12" lg="6">
                                 <CFormGroup>
                                    <CLabel htmlFor="title"> Title</CLabel>
                                    <CInput defaultValue={this.props.assignmentData.title}  name="title" onChange={this.handleChange} />
                                </CFormGroup>
                                </CCol>
                              
                               
                                </CFormGroup>
                              
                                <CFormGroup>
                                <CRow className="align-items-center ">
                                    <CCol className="col-0 col-lg-3"></CCol>
                                    <CCol   className="mb-3 col-12 col-lg-4">
                                        <CButton block color="primary" onClick={this.handleContinue}>Upload file</CButton>
                                    </CCol>
                                    <CCol  className="mb-3 col-12 col-lg-2">
                                    <CButton block variant="outline" color="danger" onClick={this.handleSet.bind(this)}>Cancel</CButton>
                                    </CCol>
                            </CRow>
                        </CFormGroup>
                      </CCardBody>
                    </CCard>
                  </CCol>
                </CRow>
            </CModalBody>
     </CModal>

          </>;
      }

}

// export default Upload



const mapStateToProps=state=>({
    fileData:state.getFiles[0]
  })
  const mapDispatchToProps=dispatch=>({
    getFileHandler:data=>dispatch(getFiles(data))
  
  })
  export default connect(mapStateToProps,mapDispatchToProps)(Upload)